const initState = {
  game: { platforms: [] },
  screen: { results: [] },
  isLoading: true,
};

const detailReducer = (state = initState, action) => {
  switch (action.type) {
    case "GET_DETAIL":
      return {
        ...state,
        game: action.payload.game,
        screen: action.payload.screen,
        isLoading: false,
      };
      break;
    case "LOADING":
      return {
        ...state,
        isLoading: true,
      };
      break;

    default:
      return { ...state };
      break;
  }
};

export default detailReducer;
