// Base URL
const baseURL = "https://api.rawg.io/api/";
const API_KEY = process.env.REACT_APP_API_KEY;
// console.log(API_KEY);
// Getting the date
const getCurrentMonth = () => {
  const month = new Date().getMonth() + 1;
  return month < 10 ? `0${month}` : month;
};
// Getting the date
const getCurrentDay = () => {
  const day = new Date().getDate();
  return day < 10 ? `0${day}` : day;
};

//  current day/month
const currentYear = new Date().getFullYear();
const currentMonth = getCurrentMonth();
const currentDay = getCurrentDay();

const currentDate = `${currentYear}-${currentMonth}-${currentDay}`;
const lastYear = `${currentYear - 1}-${currentMonth}-${currentDay}`;
const nextYear = `${currentYear + 1}-${currentMonth}-${currentDay}`;
// console.log(currentDate);

//  Popular games
const popular_games = `games?dates=${lastYear},${currentYear}&ordering=-rating&page_size=10`;
const upcoming_games = `games?dates=${currentDate},${nextYear}&ordering=-added&page_size=10`;
const new_games = `games?dates=${lastYear},${currentDate}&ordering=-released&page_size=10`;

export const popularGamesURL = () =>
  `${baseURL}${popular_games}&key=${API_KEY}`;
export const upcomingGamesURL = () =>
  `${baseURL}${upcoming_games}&key=${API_KEY}`;

export const newGamesURL = () => `${baseURL}${new_games}&key=${API_KEY}`;

//  Game Details
export const gameDetailsURL = (id) => `${baseURL}games/${id}?key=${API_KEY}`;

// Game Screenshoots
export const gameScreenshotsURL = (id) =>
  `${baseURL}games/${id}/screenshots?key=${API_KEY}`;

// Search game
export const searchGameURL = (game_name) =>
  `${baseURL}games?search=${game_name}&page_size=9&key=${API_KEY}`;
